@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.homePageDiv{
    height: 100vh;
    /* width: 90vw; */
}

.profileDiv {
    padding-top: 10vh;
    text-align: center;
    display: block;
    justify-content: center;
    align-items: center;
    margin: auto;
}

img {
    border-radius: 50%;
    border: 5px solid #E9A822;
}

.profileName{
    font-family: 'Josefin Sans', sans-serif;
    font-size: 10vmin;
    font-weight: 100;
    color: white;
}

.profileWelcome{
    padding-top: 8vmin;
    display: inline-block;
    font-family: 'Josefin Sans', sans-serif;
    font-size: 4vmin;
    font-weight: 100;
    width: 50vw;
    color: white;
}

.profileIntro{
    display: inline-block;
    font-family: 'Josefin Sans', sans-serif;
    font-size: 4vmin;
    font-weight: 100;
    width: 50vw;
    color: white;
}

.test{
    height: 100vh;
}

.divider {
    display: block;
    /* height: ; */
    border: 0;
    border-top: 1vmin solid #E9A822;
    /* width: 90vw; */
    margin: 1em 0;
    padding-top: 3vh;
    padding-bottom: 3vh;
    float: center;
    width: 80vw;
    margin-left: auto;
    margin-right: auto;
}
* {
  margin: 0;
  padding: 0;
  border: none;
  background-color: rgb(37, 41, 52);
}

.sticky-navbar {
  background-color: rgb(37,41,52);
  position: -webkit-sticky;
  /* Safari */
  position: sticky;
  top: 0;
  height: 8vmin;
  width: 100%;
  font-family: 'Lato', sans-serif;
  font-size: 3vmin;
  text-align: right;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  vertical-align: center;
  padding: 0;
  /* line-height: 0; */
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  float: right;
  height: 8vmin;
  background-color: rgb(37,41,52);
}

li {
  float: left;
}

li a {
  display: block;
  color: white;
  text-align: center;
  padding: 2vmin 20px 2vmin;
  text-decoration: none;
  text-align: center;
  height: 8vmin;
}

/* Change the link color to #111 (black) on hover */

li a:hover {
  background-color: #E9A822;
}

.is-active {
  background-color: red;
}
.contact{
    text-align: center;
    display: block;
